<template>
  <div class="index-box">
    <HeadTop title="" :isShowTop="false" :isSubHeader="true"></HeadTop>
     <div class="top-banner">
       <div class="title"></div>
       <div class="desc"></div>
       <div class="span-btn">
          
       </div>
    </div>
    <div class="about-box">
      <div class="title"></div>
      <div class="info">
        <b></b><b></b>
        <br>
        <br>
        <!-- <img src="@/assets/images/index_img.png" alt=""> -->
         <div class="chimee-container">
            <video tabindex="-1"></video>
          </div>
        <br>
        <b></b><b></b>
      </div>
    </div>
  </div>
</template>
<script>
import HeadTop from '@/components/HeadTop'
import { reactive, toRefs, computed, watch, nextTick } from "vue";
import { useStore } from "vuex";
import ChimeePlayer from 'chimee-mobile-player';
import 'chimee-mobile-player/lib/chimee-mobile-player.browser.css'
export default {
  components: {
    HeadTop
  },
  setup () {
    const store = useStore()
    const data = reactive({
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      initContract: () => {
        data.initData()
      },
      initData: () => {
      },
    })
    nextTick(() => {
      new ChimeePlayer({
        wrapper: '.chimee-container',  // video dom
        src: 'https://ktswap.github.io/static/video.mp4',
        autoplay: true,
        controls: false,
        playsInline: true,
        preload: 'auto',
        x5VideoPlayerFullscreen: true,
        x5VideoOrientation: 'landscape|portrait',
        xWebkitAirplay: true,
        muted: true
      });
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.index-box{
   padding: 15px 16px;
  .top-banner{
    padding: 16px;
    background: url('~@/assets/images/item_bg.png') no-repeat;
    background-size: cover;
    border-radius: 4px;
    margin-bottom: 30px;
    .title{
      font-size: 23px;
      color: #fff;
      font-weight: 600;
      margin-bottom: 5px;
    }
    .desc{
      font-size: 14px;
      color: rgba(255, 255, 255, 0.55);
      margin-bottom: 15px;
    }
    .span-btn{
      height: 22px;
      line-height: 22px;
      font-size: 10px;
      background: linear-gradient(0deg, rgba(255, 255, 255, 0.8) 0%, #FFFFFF 100%);
      color: #F03C85;
      padding: 0 10px;
      border-radius: 12px;
      display: inline-block;
    }
  }
  .title{
    color: #fff;
    font-size: 22px;
    margin-bottom: 20px;
  }
  .info{
    font-size: 15px;
    line-height: 24px;
    color: #fff;
    b{
      font-size: 20px;
      color: rgb(100, 197, 250);
    }
    img{
      display: block;
      width: 100%;
    }
  }
}

.chimee-container{
  position: relative;
}
.time-ad{
  background-color: transparent;
  border:0;
}
.time-ad img{
  display: block;
  width: 30px;
}
.time-ad em{
  position: absolute;
  right: -7px;
  top: 35px;
  width: 60px;
  text-align: center;
  font-size: 12px;
  background-color: rgba(156, 85, 85, 0.66);
  padding: 0 3px;
  z-index: -1;
  border-radius: 5px;
}
.time-ad cm-pp-close{
  display: none;
}
.time-ad cm-pp-body{
  padding: 0;
}
</style>
